.sauna-our-works {
    padding-top: 156px;

    @include lg-tablet {
        padding-top: 100px;
    }

    @include phone {
        padding-top: 50px;
    }

    .container {
        padding: 0;

        @include lg-max {
            padding: 0 20px;
        }
    }

    &__wrapper {
    }

    &__slider {
        margin-top: 50px;
        padding-bottom: 33px;

        @include phone {
            margin-top: 25px;
        }
    }

    &__slide {
        &--big {
            .sauna-our-works__img-wrapper {
                display: block;
                height: 520px;
            }

            @include md {
                .sauna-our-works__img-wrapper {
                    display: block;
                    height: 420px;
                } 
            }

            @include phone {
                .sauna-our-works__img-wrapper {
                    display: block;
                    height: 320px;
                }
            }
        }
    }

    &__img-grid {
        width: 100%;
        height: 100%;
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-template-rows: repeat(2, 250px);
        gap: 20px 23px;
        cursor: pointer;

        @include md {
            grid-template-rows: repeat(2, 200px);
        }

        @include phone {
            grid-template-rows: repeat(2, 150px);
        }
    }

    &__img-wrapper {
        img {
            width: 100%;
            height: 100%;
            max-width: 100%;
            object-fit: cover;
        }
    }

    &__pagination {
        position: absolute;
        bottom: 1px;
        left: 0;
        width: 100%;
        height: 1px;
        background: #791319;

        .sauna-our-works__paginaton-fill {
            position: absolute;
            bottom: -1px;
            left: 0;
            height: 4px;
            background: #791319;
            width: 100%;
            transform-origin: left top;
        }
    }
}

.sauna-our-roots {
    padding-top: 165px;

    @include lg-tablet {
        padding-top: 100px;
    }

    @include phone {
        padding-top: 50px;
    }

    .container {
        padding-left: 135px;

        @include lg-max {
            padding: 0 20px;
        }
    }

    &__content {
        margin-top: 130px;
        width: 100%;
        max-width: 1188px;

        @include lg-tablet {
            margin-top: 80px;
        }

        @include lg-phone {
            margin-top: 45px;
            flex-direction: column;
            align-items: center;
        }
    }
}

.sauna-card {
    display: flex;

    &__text-content {
        @include lg-phone {
            margin-top: 25px;
        }
    }

    &__img-wrapper {
        flex-shrink: 0;
        margin-right: 80px;
        width: 100%;
        max-width: 249px;

        @include lg-tablet {
            margin-right: 45px;
        }

        @include lg-phone {
            margin-right: 0;
        }
    }

    &__title {
        font-size: 24px;
        font-weight: 700;
        color: #672321;

        @include lg-phone {
            text-align: center;
        }
    }

    &__place {
        margin-top: 5px;
        display: block;
        font-size: 18px;
        color: #672321;

        @include lg-phone {
            text-align: center;
        }
    }

    &__contact {
        margin-top: 8px;
        display: block;
        font-size: 18px;
        color: #686868;

        @include lg-phone {
            text-align: center;
        }
    }

    &__descr {
        margin-top: 25px;
        line-height: 26px;

        @include lg-tablet {
            font-size: 18px;
        }

        @include lg-phone {
            line-height: 1.5;
            text-align: center;
        }

        @include phone {
            font-size: 16px;
        }
    }
}

.sauna-feedback {
    padding-top: 225px;

    &__form {
        margin: 0 auto;
        margin-top: 50px;
        width: 100%;
        max-width: 1151px;

        @include phone {
            margin-top: 25px;
        }
    }

    @include lg-tablet {
        padding-top: 100px;
    }

    @include phone {
        padding-top: 75px;
    }
}

.sauna-form {
    &__content {
        display: flex;

        @include lg-phone {
            flex-direction: column;
        }
    }

    &__left,
    &__right {
        flex: 1;
    }

    &__left {
        margin-right: 41px;

        @include lg-phone {
            margin-right: 0;
        }
    }

    &__label {
        display: block;

        &--with-asterisk {
            position: relative;

            .sauna-form__input {
                padding-left: 42px;
            }
            
            &::after {
                content: "*";
                position: absolute;
                top: 50%;
                left: 30px;
                transform: translateY(-50%);
                color: #ca121e;
                margin-top: 2px;
            }
        }

        &:not(:first-of-type) {
            margin-top: 34px;

            @include lg-phone {
                margin-top: 18px;
            }
        }
    }

    &__input {
        width: 100%;
        border-radius: 0;
        height: 56px;
    }

    &__textarea {
        width: 100%;
        height: 100%;
        resize: none;

        @include lg-phone {
            margin-top: 18px;
            height: 160px;
        }
    }

    input,
    textarea {
        display: block;
        border: none;
        padding: 22px 28px 17px; 
        width: 100%;
        background: #ececec;

        &::placeholder {
            color: #959595;
        }
    }

    &__btn {
        display: block;
        margin: 0 auto;
        align-self: center;
        margin-top: 40px;
        border: 2px solid #ffba5f;
        padding: 18px 43px 16px;
        background: transparent;
        font-size: 16px;
        font-weight: 500;
        text-transform: uppercase;
        color: #672321;
        transition: color 0.25s ease, background-color 0.25s ease, border-color 0.25s ease;

        &:hover {
            border-color: #672321; 
            background: #672321;
            color: #fff;
        }
        
        &:active {
            border-color: darken(#672321, 5%); 
            background: darken(#672321, 5%);
            color: #fff;
        }
        
        @include lg-phone {
            line-height: 1.4;
        }

        @include phone {
            margin-top: 35px;
            font-size: 12px;
        }
    }
}