*,
*::before,
*::after {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html {
}

body {
    font-family: "Muller", sans-serif;
    width: 100%;
    height: 100%;
}

ul,
ol {
    list-style: none;
    padding: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    margin: 0;
    padding: 0;
}

a {
    text-decoration: none;
    color: inherit;
}

.btn--reset {
    border: none;
    background: none;
    cursor: pointer;
}

.container {
    margin: 0 auto;
    width: 100%;
    max-width: 1600px;
    padding: 0 50px;
    display: block;

    @media only screen and (max-width: 1023px) {
        padding: 0 30px;
    }

    @media only screen and (max-width: 459px) {
        padding: 0 20px;
    }

}