@mixin lg-max {
    @media (max-width: 1700px) {
        @content;
    }
}

@mixin lg {
    @media (max-width: 1500px) {
        @content;
    }
}

@mixin md {
    @media (max-width: 1200px) {
        @content;
    }
}

@mixin desktop {
    @media (max-width: 1024px) {
        @content;
    }
}

@mixin lg-tablet {
    @media (max-width: 992px) {
        @content;
    }
}

@mixin tablet {
    @media (max-width: 768px) {
        @content;
    }
}

@mixin lg-phone {
    @media (max-width: 576px) {
        @content;
    }
}

@mixin phone {
    @media (max-width: 425px) {
        @content;
    }
}

@mixin sm-phone {
    @media (max-width: 375px) {
        @content;
    }
}