.sauna-chose {
    padding-top: 80px;
    background: #faf9f7;

    @include tablet {
        padding-bottom: 100px;
    }

    @include phone {
        padding-top: 40px;
        padding-bottom: 50px;
    }

    .container {
        padding: 0;

        @include lg-max {
            padding: 0 20px;
        }
    }
    
    &__slider-wrapper {
        position: relative;
    }

    &__slider {
        margin-top: 35px;
        max-width: calc(100% - 130px);
        width: 100%;
    }

    &__arrow {
        position: absolute;
        z-index: 10;
        top: 50%;
        transform: translateY(-50%);

        border: 2px solid #ffba5f;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 50px;
        height: 55px;

        background: #fff;
        cursor: pointer;
        transition: background-color 0.25s ease-out;

        &:hover {
            background: lighten(#ffba5f, 25%);
        }

        img {
            width: 10px;
            height: 17px;
            max-width: 100%;
        }

        &--next {
            right: 0;
        }
    }

    &__slider {
        position: relative;
        padding-bottom: 112px;

        @include tablet {
            padding-bottom: 0;
        }
    }

    &__item {
        position: relative;
        display: flex;
        flex-direction: column;
        align-items: center;
        border: 2px solid transparent;
        padding-top: 84px;
        width: 100%;
        max-width: 277px;
        height: 350px;
        cursor: pointer;
        transition: background-color 0.25s ease, border-color 0.25s ease;

        &:hover {
            background: #fff;
            border: 2px solid lighten($color: #ffba5f, $amount: 15%);

            .sauna-chose__radio-btn {
                &::before {
                    opacity: 0.4;
                }
            }
        }

        @include tablet {
            padding: 45px 20px;
            height: 100%;
        }

        @include lg-phone {
            margin: 0 auto;
        }
    }

    &__img-wrapper {
        position: relative;
        width: 148px;
        height: 148px;

        img {
            width: 100%;
            height: 100%;
            max-width: 100%;
            object-fit: cover;
        }
    }

    &__radio-btn {
        position: relative;
        display: block;
        margin-top: 40px;
        border-radius: 50%;
        width: 40px;
        height: 40px;
        background: #e2e2e2;

        &::before {
            content: "";
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            border-radius: 50%;
            width: 22px;
            height: 22px;
            background: #ca121e;
            opacity: 0;
            transition: opacity 0.25s ease-in;
        }
    }

    &__radio {
        position: absolute;
        opacity: 0;
        cursor: pointer;
        height: 0;
        width: 0;

        &:checked ~ label {
            background: #fff;
            border: 2px solid #ffba5f;

            .sauna-chose__radio-btn {
                &::before {
                    opacity: 1;
                }
            }
        }
    }
}

.sauna-size {
    position: absolute;
    top: 0;
    width: 100%;
    transform: translateY(-100%);
    display: flex;
    flex-direction: column;
    align-items: center;

    &__size {
        position: relative;
        top: 2px;
        font-size: 12px;
        font-weight: 400;
        color: #791219;
    }

    &__arrow-wrapper {
        width: 100%;
        height: 17px;
        background-image: url("../img/size-arrow-width.png");
        background-position: center;
        background-size: cover;
        background-repeat: no-repeat;
    }

    &--height {
        top: 60px;
        left: -92px;
        transform: rotate(270deg);
        
        @include md {
            top: 59px;
            left: -82px;
        }
    }
}

.sauna-title {
    font-size: 40px;
    font-weight: 700;
    line-height: 1.2;
    color: #672321;
    text-align: center;

    @include lg-phone {
        font-size: 34px;
    }
    
    @include phone {
        font-size: 24px;
    }
}

.estimate {
    position: relative;

    .container {
        padding: 0;

        @include lg-max {
            padding: 0 20px;
        }
    }

    .sauna-tabs__list {
        padding: 130px 0 96px;

        @include md {
            padding: 130px 50px 96px;
        }

        @include lg-tablet {
            padding: 80px 50px;
        }

        @include lg-phone {
            padding: 50px 20px;
        }
    }

    .sauna-tabs__content {
        display: flex;
        flex-direction: column;
        margin: 0 auto;
        width: 100%;
        max-width: 1105px;
    }

    &::before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        z-index: -1;

        width: 100%;
        height: 268px;
        background: #faf9f7;
    }

    &__title {
        span {
            color: #ca121e;
        }

        @include lg-tablet {
            line-height: 1.4;
        }

        @include phone {
            font-size: 24px;
        }
    }

    &__content {
        display: flex;
        margin-top: 40px;

        @include lg-tablet {
            flex-direction: column;
        }
    }

    &__left {
        display: flex;
        flex-direction: column;
        flex-shrink: 0;
        margin-right: 100px;
        width: 100%;
        max-width: 190px;

        @include lg-tablet {
            max-width: unset;
            flex-direction: row;
            align-items: flex-start;
            justify-content: space-between;
        }

        @include tablet {
            margin-right: 0;
        }

        @include phone {
            flex-direction: column;
        }
    }

    &__control {
        position: relative;
        border: 2px solid transparent;
        border-radius: 0;
        padding: 13px 20px 11px;
        width: 152px;
        height: 48px;
        font-size: 20px;
        font-weight: 500;
        line-height: 1;
        text-align: left;
        color: #672321;

        &:not(&.active):hover {
            color: #a0161f;
        }

        &:not(:first-of-type) {
            margin-top: 20px;
        }

        &:nth-child(2) {
            margin-bottom: 25px;
        }

        &::after {
            display: none;
            content: "";
            position: absolute;
            top: -2px;
            right: -55px;
            height: 0;
            width: 0;
            transform: translateX(100%);
            border: 24px solid transparent;
            border-right: 29px solid #ffba5f;
            transform: rotate(180deg);
        }

        &::before {
            display: none;
            content: "";
            position: absolute;
            top: 0;
            right: -49px;
            z-index: 1;
            height: 0;
            width: 0;
            border: 22px solid transparent;
            border-right: 27px solid #fff;
            transform: rotate(180deg);
        }

        &.active {
            border-color:#ffba5f;
            color: #ca121e;

            &::after {
                display: block;
            }

            &::before {
                display: block;
            }
        }

        @include lg-tablet {
            &:not(:first-of-type) {
                margin: 0;
            }
    
            &:nth-child(2) {
                margin: 0;
            }
        }

        @include tablet {
            padding: 13px 15px 11px;
            width: unset;

            &.active {
                &::before,
                &::after {
                    display: none;
                }
            }
        }

        @include phone {
            width: 100%;
            text-align: center;

            &:nth-child(2) {
                margin-top: 10px;
            }
        }
    }

    &__tab-content {
        position: absolute;
        left: -3400px;
        opacity: 0;
        transition: opacity 0.35s ease-in 0.2s;

        &.active {
            position: relative;
            visibility: visible;
            opacity: 1;
            left: 0;
        }

        @include tablet {
            display: grid;
            grid-template-columns: 1fr;
            gap: 40px;
        }
    }

    &__show-estim {
        align-self: center;
        display: block;
        margin-top: auto;
        border-bottom: 1px dashed #ca121e;
        border-radius: 0;
        font-size: 18px;
        font-weight: 400;
        color: #ca121e;
        transition: opacity 0.25s ease;

        &:hover {
            opacity: 0.8;
        }

        &--disable {
            color: #d1d1d1;
            border-color: #d1d1d1;

            &:hover {
                opacity: 1;
            }
        }

        @include lg-tablet {
            margin: 0;
        }

        @include phone {
            margin-top: 10px;
        }
    }

    &__right {
        flex: 1;
        padding-top: 17px;

        @include lg-tablet {
            padding-top: 0;
            margin-top: 40px;
        }
    }

    &__service {
        display: flex;
        align-items: center;
        color: #984046;
        font-size: 18px;
        line-height: 1;

        &--hide {
            display: none;
        }

        &:not(:first-of-type) {
            margin-top: 35px;
        }

        @include lg-tablet {
            font-size: 16px;
        }

        @include tablet {
            margin-top: 0;
            justify-content: space-between;

            &:not(:first-of-type) {
                margin-top: 0;
            }
        }
    }

    &__key {
        padding-right: 11px;
    }

    &__value {
        padding-left: 11px;

        @include tablet {
            text-align: right;
        }
    }

    &__hyphens {
        flex: 1 100px;
        height: 2px;
        background-image: url("../img/hyphens.png");

        @include tablet {
            background-size: 61px 10px;
            background-repeat: no-repeat;
            background-position: center;
        }
    }

    &__btn {
        align-self: center;
        margin-top: 95px;
        border: 2px solid #ffba5f;
        padding: 18px 115px 16px;
        background: transparent;
        font-size: 16px;
        font-weight: 500;
        text-transform: uppercase;
        color: #672321;
        transition: color 0.25s ease, background-color 0.25s ease, border-color 0.25s ease;

        &:hover {
            border-color: #672321; 
            background: #672321;
            color: #fff;
        }
        
        &:active {
            border-color: darken(#672321, 5%); 
            background: darken(#672321, 5%);
            color: #fff;
        }

        @include tablet {
            margin-top: 50px;
        }

        @include lg-phone {
            line-height: 1.3;
        }

        @include  phone {
            padding: 18px 25px 15px;
        }
    }

    &__assembly {
        margin-top: 80px;
        display: flex;

        @include lg-tablet {
            align-items: center;
            justify-content: space-between;
        }

        @include phone {
            flex-direction: column;
        }
    }

    &__option {
        flex: 1;

        &:first-of-type {
            margin-right: calc(80 * 100vw / 768);
        }

        @media (max-width: 600px) {
            &:first-of-type {
                margin-right: 20px;
            }
        }

        @include phone {
            &:first-of-type {
                margin-right: 0;
            }

            &:last-of-type {
                margin-top: 40px;
            }
        }
    }
}

.sauna-option {
    position: relative;

    &__radio {
        position: absolute;
        opacity: 0;
        cursor: pointer;
        height: 0;
        width: 0;

        &:checked ~ label {
            .sauna-option__radio-btn{
                &::before {
                    opacity: 1;
                }
            }

            .sauna-option__price {
                color: #ca121e;
            }
    
            .sauna-option__guarantee {
                color: #ca121e;
            }
        }
    }

    &__item {
        display: flex;
        cursor: pointer;

        &:hover {
            .sauna-option__radio-btn{
                &::before {
                    opacity: 0.4;
                }
            }

            .sauna-option__price {
                color: #d17777;
            }
    
            .sauna-option__guarantee {
                color: #d17777;
            }
            transition: color 0.25s ease;
        }

        @include lg-tablet {
            flex-direction: column;
            align-items: center;
            justify-content: space-between;
            text-align: center;
        }
    }

    
    &__radio-btn {
        position: relative;
        display: block;
        margin-right: 35px;
        border-radius: 50%;
        flex-shrink: 0;
        width: 40px;
        height: 40px;
        background: #e2e2e2;

        &::before {
            content: "";
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            border-radius: 50%;
            width: 22px;
            height: 22px;
            background: #ca121e;
            opacity: 0;
            transition: opacity 0.25s ease-in;
        }

        @include lg-tablet {
            margin: 0;
        }
    }
    
    &__descr {
        display: block;
        font-size: 20px;
        line-break: loose;
        line-height: 1.2;
        color: #984046;

        @include lg-tablet {
            font-size: 18px;
        }
    }

    &__price {
        display: block;
        margin-top: 20px;
        font-size: 50px;
        font-weight: 700;
        line-height: 1;
        color: #d1d1d1;
        transition: color 0.25s ease;

        @include lg-tablet {
            font-size: 40px;
        }
    }

    &__guarantee {
        margin-top: 20px;
        font-size: 18px;
        line-height: 25px;
        color: #d1d1d1;
        transition: color 0.25s ease;

        @include lg-tablet {
            display: block;
            margin-top: 20px;
        }
    }
}