.sauna-hero {
    position: relative;
    height: 687px;

    @include lg-tablet {
        height: 450px;
    }

    &__slider,
    &__content {
        height: 100%;
    }

    &__slider {
        .swiper-slide {
            width: 50%;
        }
    }

    &__content {
        position: absolute;
        bottom: 0;
        padding-top: 30px;
        height: 50%;
        width: 100%;
        z-index: 11;

        .container {
            padding-left: 20px;

            @include lg-max {
                padding: 0 20px;
            }
        }

        @include lg-tablet {
            top: 50%;
            left: 50%;
            bottom: unset;
            transform: translate(-50%, -50%);
        }
    }

    &__title {
        font-size: 50px;
        font-weight: 700;
        text-transform: uppercase;
        letter-spacing: 1px;
        color: #fff;

        @include lg-max {
            text-align: center;
        }

        @include lg {
            font-size: 45px;   
        }

        @include md {
            line-height: 1.4;
        }

        @include lg-tablet {
            font-size: 35px;
        }

        @include phone {
            font-size: 28px;
        }
    }

    &__arrow {
        display: inline-flex;
        align-items: center;
        justify-content: center;
        padding: 0 85px;

        img {
            width: 160px;
            height: 37px; 
        }

        @include lg {
            padding: 0 10px;
        }

        @include lg-tablet {
            img {
                width: 92px;
                height: 26px;
            }
        }
    }
