    &__btn {
        position: relative;
        overflow: hidden;
        display: block;
        margin: 0 auto;
        margin-top: 100px;
        // border: 2px solid #ffba5f;
        border-radius: 0;
        width: 400px;
        height: 55px;

        font-size: 18px;
        font-weight: 500;
        letter-spacing: 0.75px;
        text-transform: uppercase;
        color: #fff;

        &::after {
            content: "";
            position: absolute;
            z-index: 100;
            top: 2px;
            left: 2px;
            width: 25%;
            height: 75%;

            transform: translate(-100%, -100%);

            background: #fff;
            animation: cubeAnima 6s linear infinite;
        }

        &::before {
            content: "";
            position: absolute;
            z-index: 100;
            top: 2px;
            left: 2px;
            width: 25%;
            height: 75%;

            transform: translate(-100%, -100%);

            background: #fff;
            animation: cubeAnimaRev 6s linear infinite;
        }

        span {
            position: relative;
            z-index: 10;
            display: flex;
            align-items: center;
            justify-content: center;
            width: 100%;
            height: 100%;
            border: 2px solid #ffba5f;
            border-radius: 0;
            background: rgba(0,0,0,0.6);
            transition: background-color 0.25s ease;
        }

        &:hover {
            span {
                background-color: rgba(0,0,0,0.75);
            }
        }

        @include lg {
            margin-top: 60px;
        }

        @include lg-phone {
            width: 320px;
        }

        @include phone {
            width: 280px;
            line-height: 1.4;
            font-size: 17px;
        }
    }

    &__img {
        height: 100%;
        width: 100%;
        max-width: 100%;
        object-fit: cover;
    }
}

@keyframes rotate {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(360deg);
    }
}

@keyframes cubeAnima {
    0% {
        left: 2px;
        top: 2px;
    }

    37% {
        left: 498px;
        top: 2px;
    }

    50% {
        top: 94px;
        left: 498px;
    }

    88% {
        left: 2px;
        top: 94px;
    }

    100% {
        left: 2px;
        top: 2px;
    }
}

@keyframes cubeAnimaRev {
    0% {
        top: 94px;
        left: 498px;
    }

    37% {
        left: 2px;
        top: 94px;
    }

    50% {
        left: 2px;
        top: 2px; 
    }

    88% {
        left: 498px;
        top: 2px;
    }

    100% {
        top: 94px;
        left: 498px;
    }
}
