.sauna-promo {
    position: relative;
    padding-top: 71px;
    padding-bottom: 30px;
    border-top: 4px solid #f2f2f2;

    &::before {
        content: "";
        position: absolute;
        bottom: 0;
        left: 0;
        z-index: -1;
        width: 100%;
        height: 310px;
        background: #faf9f7;
    }

    .container {
        padding: 0;

        @include lg-max {
            padding: 0 20px;
        }
    }

    &__text-content {
        display: flex;
        margin: 0 auto;
        width: 100%;
        max-width: 1066px;

        @include lg-tablet {
            align-items: center;
        }

        @include lg-phone {
            flex-direction: column;
            text-align: center;
        }
    }

    &__img-wrapper {
        margin-right: 65px;
        width: 197px;
        flex-shrink: 0;
        height: 199px;

        img {
            width: 100%;
            height: 100%;
            max-width: 100%;
            object-fit: contain;
        }

        @include lg-phone {
            margin-right: 0;
        }
    }

    &__text {
        padding-top: 58px;

        @include lg-tablet {
            padding-top: 20px;
        }
    }

    &__tabs {
        margin-top: 58px;
    }
}

.sauna-text {
    font-size: 20px;
    line-height: 1.2;
    color: #984046;

    @include phone {
        font-size: 18px;
    }
}

.sauna-tabs {
    position: relative;

    &__loader {
        visibility: hidden;
        pointer-events: none;
        opacity: 0;
        position: absolute;
        inset: 0;
        z-index: 1000;
        background: #fff;
        display: flex;
        justify-content: center;
        align-items: center;
        transition: opacity 0.15s ease;

        &.active {
            opacity: 1;
            visibility: visible;
        }
    }


    &__controls {
        display: grid;
        grid-template-columns: repeat(auto-fit, minmax(190px, 1fr));
        grid-template-rows: minmax(80px, auto);
        column-gap: 8px;
        margin: 0 auto;
        width: 100%;
        max-width: 1066px;

        @include tablet {
            grid-template-columns: repeat(3, minmax(auto, 190px));
            justify-content: center;
        }

        @include lg-phone {
            grid-template-columns: 1fr;
            grid-auto-rows: minmax(80px, auto);
        }
    }

    &__control  {
        position: relative;
        background: #f3eded;
        transition: background-color 0.25s ease;

        button {
            position: relative;
            z-index: 100;
            display: flex;
            align-items: center;
            justify-content: center;
            width: 100%;
            height: 100%;
            font-size: 20px;
            line-height: 30px;
            color: #882b32; // #672321
            transition: color 0.25s ease;

            span {
                margin-left: 6px;
                display: none;
                opacity: 0;
                transition: opacity 0.25s ease;
            }
        }

        br {
            display: none;
        }

        &:hover {
            background: lighten($color: #f3eded, $amount: 2%);

            button {
                color: #ca121e;
            }
        }

        &.active {
            background: #fff;
            z-index: 10;

            &::after {
                content: "";
                position: absolute;
                inset: 0;
                box-shadow: 0 0 30px rgba(0, 0, 0, 0.1);
            }

            &::before {
                content: "";
                z-index: 10;
                position: absolute;
                bottom: -30px;
                left: 50%;
                width: 150%;
                height: 30px;
                transform: translateX(-50%);
                background: #fff;

                @media (max-width: 1288px) {
                    width: 100%;
                    left: 0;
                    bottom: -29px;
                    transform: translateX(0);
                }

                @include lg-phone {
                    display: none;
                }
            }

            button {
                color: #ca121e;

                span {
                    display: block;
                    opacity: 1;
                }
            }
        }

        @include tablet {
            display: flex;
            align-items: center;
            justify-content: center;
            padding: 0 10px;

            button {
                flex-wrap: wrap;
                height: unset;
            }
        }

        @include phone {
            button {
                font-size: 18px;
            }
        }
    }

    &__list {
        position: relative;
        overflow: hidden;
        padding: 80px 143px 109px 70px;
        box-shadow: 0 0 30px rgba(0, 0, 0, 0.1);
        background: #fff;

        @include lg {
            padding: 80px 50px 109px;
        }

        @include lg-phone {
            padding: 50px 20px;
        }
    }

    &__gallery-item {
        position: absolute;
        left: -3400px;
        opacity: 0;
        transition: opacity 0.7s ease-in 0.1s;

        &.active {
            display: block;
            position: relative;
            visibility: visible;
            opacity: 1;
            left: 0;

            .sauna-gallery__preview {
                opacity: 1;
            }

            .sauna-gallery__thumbs {
                .sauna-gallery__slide {
                    opacity: 1;
                    transform: translateY(0);
                }
            }

            .sauna-tabs__text-content {
                > * {
                    opacity: 1;
                    transform: translateX(0);
                }
            }
        }
    }

    &__item {
        position: absolute;
        left: -3500px;
        width: 100%;
        opacity: 0;
        transition: opacity 0.7s ease-in 0.1s;

        &.active {
            position: relative;
            visibility: visible;
            opacity: 1;
            left: 0;
        }

        .sauna-tabs__content {
            display: block;
        }
    }


    &__content {
        display: flex;

        @include lg {
            justify-content: space-between;
        }

        @include lg-tablet {
            flex-direction: column;
        }
    }

    &__text-content {
        > * {
            transform: translateX(400px);
            opacity: 0;
            transition: opacity 0.3s linear 0.45s, transform 0.5s ease 0.50s;
        }

        > :nth-child(2) {
            transition: opacity 0.3s linear 0.45s, transform 0.55s ease 0.53s;
        }

        > :nth-child(3) {
            transition: opacity 0.3s linear 0.45s, transform 0.60s ease 0.56s;
        }

        > :nth-child(4) {
            transition: opacity 0.3s linear 0.45s, transform 0.65s ease 0.59s,
            color 0.25s ease,
            background-color 0.25s ease,
            border-color 0.25s ease;
        }

        > :nth-child(5) {
            transition: opacity 0.3s linear 0.45s,
            transform 0.70s ease 0.62s,
            color 0.25s ease,
            background-color 0.25s ease,
            border-color 0.25s ease; 
        }

        > :nth-child(6) {
            transition: opacity 0.3s linear 0.45s, transform 0.75s ease 0.65s;
        }

        @include lg-tablet {
            margin-top: 40px;
        }
    }


    &__features {
        margin-top: 60px;
        padding-left: 17px;

        @include md {
            margin-top: 30px;;
        }
    }

    &__feature {
        position: relative;
        display: block;
        font-size: 18px;
        font-weight: 700;
        color: #672321;

        &::before {
            content: "";
            position: absolute;
            top: 4px;
            left: -18px;

            border-radius: 50%;
            width: 7px;
            height: 7px;
            background: #ffba5f;
        }

        &:not(:first-of-type) {
            margin-top: 18px;
        }
    }

    &__term {
        margin-top: 60px;
        font-size: 18px;

        @include md {
            margin-top: 30px;
        }
    }

    &__btn {
        &--chose {
            margin-top: 70px;
            border: 2px solid #ffba5f;
            padding: 21px 29px 17px ;
            display: block;
            max-width: 290px;
            width: 100%;
            font-size: 16px;
            line-height: 18px;
            font-weight: 700;
            text-transform: uppercase;
            color: #672321;

            &:hover {
                border-color: #672321; 
                background: #672321;
                color: #fff;
            }
            
            &:active {
                border-color: darken(#672321, 5%); 
                background: darken(#672321, 5%);
                color: #fff;
            }

            @include md {
                margin-top: 45px;
            }

            @include phone {
                padding: 14px 17px 10px;
                font-size: 14px;
                text-align: center;
                line-height: 1.4;
            }
        }

        &--watch {
            display: flex;
            align-items: center;
            margin-top: 50px;
            padding: 3px 0;
            font-size: 18px;
            font-weight: 400;
            color: #672321;
            line-height: 1;

            .icon-wrapper {
                flex-shrink: 0;
                margin-right: 11px;
                width: 30px;
                height: 30px;

                img {
                    width: 100%;
                    height: 100%;
                    max-width: 100%;
                    object-fit: contain;
                }
            }

            .sauna-tabs__btn-content {
                padding-top: 3px;
            }
        }

        @include lg-phone {
            margin: 0 auto;
            margin-top: 35px;
        }

        @include phone {
            flex-direction: column;
            font-size: 15px;

            .icon-wrapper {
                margin: 0;
                margin-bottom: 10px;
            }

            .sauna-tabs__btn-content {
                max-width: 90%;
            }
        }
    }
}

.sauna-tabs__text-content {
    .sauna-tabs__btn--watch {
        &:hover {
            opacity: 0.8;
        }

        &:active {
            opacity: 0.7;
        }
    }
}

.sauna-gallery {
    margin-right: 107px;
    max-width: 656px;

    @include lg {
        margin-right: 40px;
    }

    @include md {
        max-width: 420px;
    }

    @include lg-tablet {
        margin-right: 0;
        width: 100%;
        max-width: calc(800 * 100vw / 992);
    }

    @include lg-phone {
        margin: 0 auto;
    }

    &__preview {
        width: 656px;
        height: 420px;
        opacity: 0;
        transition: opacity 0.25s linear 0.45s;

        @include md {
            width: 420px;
            height: 300px;
        }

        @include lg-tablet {
            width: 100%;
            height: 400px;
        }

        @include phone {
            height: 180px;
        }
    }

    &__thumbs {
        position: relative;
        margin-top: 13px;
        padding: 0 67px;
        padding-bottom: 4px;

        .sauna-gallery__slide  {
            height: 118px;
            cursor: pointer;
            transform: translateY(-400px);
            opacity: 0;
            transition: opacity 0.43s ease-in 0.55s, transform 0.45s ease 0.55s;

            &::after {
                content: "";
                position: absolute;
                left: 0;
                bottom: 0px;
                height: 4px;
                width: 100%;
                background: #ca121e;
                opacity: 0;
                transition: opacity 0.25s ease;
            }
            
            &:nth-child(2) {
                transition: opacity 0.3s ease-in 0.55s, transform 0.55s ease 0.55s;
            }

            &:nth-child(3) {
                transition: opacity 0.3s ease-in 0.55s, transform 0.65s ease 0.55s;
            }

            &:nth-child(4) {
                transition: opacity 0.3s ease-in 0.55s, transform 0.75s ease 0.55s;
            }

            &:nth-child(5) {
                transition: opacity 0.3s ease-in 0.55s, transform 0.85s ease 0.55s;
            }

            &:nth-child(6) {
                transition: opacity 0.3s ease-in 0.55s, transform 0.95s ease 0.55s;
            }

            @include md {
                height: 80px;
            }

            @include lg-tablet {
                height: 100px;
            }

            @include phone {
                height: 60px;
            }
        }

        .swiper-slide-thumb-active {
            &::after {
                opacity: 1;
            }
        }

        @include md {
            .swiper-container {
                width: 100%;
            }
        }

        @include phone {
            padding: 0 24px;
        }
    }

    &__arrow--prev {
        position: absolute;
        top: 50%;
        left: 20px;
        z-index: 100;
        transform: translateY(-50%);

        width: 10px;
        height: 17px;
        background-image: url("../img/slider-arrow-left.png");
        background-position: center;
        background-size: cover;
        cursor: pointer;

        @include phone {
            left: 0;
        }
    }

    &__arrow--next {
        position: absolute;
        top: 50%;
        right: 20px;
        z-index: 100;
        transform: translateY(-50%);

        width: 10px;
        height: 17px;
        background-image: url("../img/slider-arrow-right.png");
        background-position: center;
        background-size: cover;
        cursor: pointer;

        @include phone {
            right: 0;
        }
    }
}

.sauna-gallery__slide  {
    img {
        width: 100%;
        height: 100%;
        max-width: 100%;
        object-fit: cover;
    }
}